.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

h2.text-center {
    margin-bottom: 2rem;
}

form {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.mb-3 {
    margin-bottom: 1.5rem;
}

.form-label {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.form-control {
    width: 100%;
    padding: 0.8rem 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.form-control:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.form-select {
    width: 100%;
    padding: 0.8rem 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1.2rem;

background-color: #fff;
margin-bottom: 1rem;
appearance: none;
}

.form-select:focus {
outline: none;
border-color: #007bff;
box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.form-text {
font-size: 1rem;
color: #6c757d;
margin-top: 0.5rem;
}

.form-control-file {
width: 100%;
padding: 0.8rem 1rem;
border: 1px solid #ccc;
border-radius: 5px;
font-size: 1.2rem;
margin-bottom: 1rem;
}

.form-button {
padding: 0.8rem 1.5rem;
border-radius: 5px;
font-size: 1.2rem;
background-color: #007bff;
color: #fff;
border: none;
cursor: pointer;
margin-top: 1rem;
}

.form-button:hover {
background-color: #0069d9;
}

.form-button:active {
background-color: #0062cc;
}

.form-button:disabled {
background-color: #6c757d;
cursor: not-allowed;
}

.form-control:disabled, .form-select:disabled, .form-control-file:disabled {
background-color: #e9ecef;
color: #6c757d;
cursor: not-allowed;
}